.footer-wrapper {
      border-top: 1px solid #ccc;
      background: #fbfbfb;
      display: flex;
      justify-content: space-between;
      padding: 15px;
      @include susy-media(phone-only){
         flex-wrap: wrap;
         padding: 0;
      }
      footer {
         width: 100%;
         @include susy-media(tablet-portrait-up){
            display: flex;
            justify-content: space-between;
         }
      }
      .logo {
         @include susy-media(phone-only){
               padding: $post-spacing;
               text-align: center;
               width: 100%;
               background: $footer-color-logo-background;
         }
         a { 
            color: #024b79;
            font-weight: bold;

         }
      }
      .contact-footer {
            display: flex;
            @include susy-media(phone-only){
             flex-direction: column;
            }
            margin-bottom: $spacing-small;
         .field {
            color: #999;
            font-weight: normal;      
            font-size: 13px;
            padding: 0 20px;
            line-height: 1.75em;
         }
      }
   }