body, html {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.page.thankyou {
    flex: 1;
    display: flex;
    flex-direction: column;
  .footer-wrapper {
    margin-top: auto;
  }
}

// FRONTPAGE
 header {
  // position: absolute;
  // top: 0;
  // right: 0;
  // left: 0;
  // z-index: 999;
 }
 .nav-wrapper {
    padding: 0 15px;
 }
 nav {
    background: #fff;
    .brand-logo {
      width: span(4 of 16);
      @include susy-media(phone-only) {
        width: auto;
      }
    }
    ul {
      width: span(10 of 16 last);
      display: flex;
      justify-content: space-between;
    }
    .dropdown-content {
      min-width: 150px;
    }
    ul#dropdown4.dropdown-content {
      min-width: 180px;
    }
  }

// DEFAULT LAYOUT
.page {
  @include layout( $full-width );
  @include full;
}

// FRONTPAGE
.slideshow-section{
  @include full;
}

.aboutus-section,
.rooms-section,
.reviews-section,
.contact-rooms--section,
.thankyou-section,
.contact-section,
.details-section,
.photos-section {
  @include layout( $columns16 );
  @include container;
  margin-bottom: $section-spacing;
  @include susy-media(phone-only) {
    padding: 0 $section-horizontal-spacing;
  }
}
.reviews-section,
.reviews-rooms--section {
  @include susy-media(phone-only) {
    width: 100%;
    padding-bottom: $section-spacing;
  }
}
.aboutus-section,
.thankyou-section,
.content {
  @include break;
}
.aboutus-section {
  display: flex;
  @include susy-media(phone-only) {
    flex-wrap: wrap;
  }
  .description {
    @include span( 16 of 16);
    .abouts-us {
      display: block;
      float: left;
      width: 100%;
    }
  }
  // @include susy-media(phone-only) {
  //    .description {
  //     width: 100%;
  //     margin: 0;
  //   }
  //   .sidebar {
  //       width: 100%;
  //       margin: 0;
  //       display: flex;
  //       flex-wrap: nowrap;
  //       justify-content: flex-start;
  //   }   
  // }
}

// ROOM PAGE
.mobile-title-section,
.description-section,
.awards-section,
.facilities-section,
.rates-section,
.reviews-rooms--section,
.photos-rooms--section,
.contact-rooms--section {
  @include layout( $columns16 );
  @include container;
  margin-bottom: $spacing-xxx-large;
  @include susy-media(phone-only) {
    padding: 0 $section-horizontal-spacing;
  }
  @include susy-media(big-desktop-up) {
    padding: 0;
  }
}
// .contact-rooms--section {
//   margin-bottom: 0;
// }
.facilities-section {
  @include susy-media(phone-only) {
    margin: 0;
  }
}
.description-section {
   @include break;
} 
.rooms>.footer-wrapper>.section-footer>.contact-footer {
  @include susy-media(phone-only) {
    display: none;
  }
}

// RATES PAGE
.rates-section {
  margin-top: 25px;
} 
// GALLERY PAGE
.gallery {
  margin-top: 25px;
}
// CONTACT PAGE

/* MAP */
#map-canvas {
   width: 100%;
}
.map-section {
  @include full;
  margin-bottom: 40px;
}
.contact-contact--section {
  @include layout( $columns16 );
  @include container;
  @include break;
  margin-bottom: 10rem;
}
// USEFUL LINKS PAGE
.useful-section {
  margin-top: 25px;
  @include layout( $columns16 );
  @include container;
  @include break;
}

/* VILLAS GALLERY */
.gallery-slideshow {
  height: 100%;
  .post-image {
     align-items: flex-start;
  }
}
.gallery-thumbnails {
  display: flex;
  flex-wrap: nowrap;
  position: absolute;
  bottom: 33px;
  z-index: 52;
  background: #fff;
}


// SUGGESTIONS   
.section-suggestions {
  @include layout( $columns16 );
  @include container;
  @include break;
  .main-suggestions {
     @include span( 11 of 16 );
     @include susy-media(phone-only) {
      @include span( 16 of 16 );
        @include container;
        padding: 0 $section-spacing;
     }
  }
  .sidebar-suggestions {
     @include span( 5 of 16 last );
     @include susy-media(phone-only) {
      display: none;
     }
  }
  .scrollpsy-block {
     margin-top: 2em;
  }
}
// FOOTER   
.footer {
  @include layout( $symmetric );
  @include container;
}