@charset "utf-8";
$baseurl: vigliabeach;
@import "reset-and-normalize";
@import "tokens/design-tokens";
// SUSY
@import "susy-config";
@import "susy";
// 
// FONTS
// 
@import "fonts/googlecss/myGoogleFonts";

// POST-CSS Rensponsive
html {
  font-size: responsive;
  min-font-size: 16px;
  max-font-size: 16px;
  lower-font-range: 420px;
  upper-font-range: 1280px;
}

$slideshow-font: 'Reem Kufi', 'Merriweather', 'Cambria', 'Georgia', 'serif';
$logo-font: 'Merriweather', 'Cambria', 'Georgia', 'serif';
$body-font: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';

body {
  font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
}

.brand-logo,
.aboutus-frontpage h1,
.footer-wrapper .logo {
  font-family: 'Merriweather', 'Cambria', 'Georgia', 'serif';
}

.slideshow-title {
  font-family: 'Reem Kufi', 'Merriweather', 'Cambria', 'Georgia', 'serif';
}

.aboutus-frontpage h1 {
  font-size: 3.8rem;

  @include susy-media(phone-only) {
    font-size: 3.3rem;
    line-height: 1.25;
  }
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

p {
  line-height: 27px;
  font-size: 16px;
  color: #8a8a8a;
}

// MATERIALIZE
@import "materialize";
// OWL SLIDESHOW
@import "owl.carousel/src/scss/owl.carousel.scss";
@import "owl.carousel/src/scss/owl.theme.default.scss";
// PARSLEY
@import "parsleyjs/src/parsley.css";

// 
// LAYOUT
// 
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  text-rendering: optimizeLegibility;
  background-color: #fff;
}

img {
  width: 100%;
  display: block;
}

ul,
menu,
dir {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li {
  list-style: none;
  list-decoration: none;
}

a {
  text-decoration: none;
  line-height: inherit;
  background-color: transparent;
}

// 
// Styles
// 
@import "styles/stylesHeader",
"styles/stylesFrontpage",
"styles/stylesFooter",
"styles/stylesPrices",
"styles/stylesRooms",
"styles/stylesFacilities",
"styles/stylesGallery",
"styles/stylesForm",
"styles/stylesUseful",
"styles/stylesMap",
"layouts/layout"
;

.recaptcha {
  width: 100%;
  float: left;
}

.hidden {
  display: none;
}