
// USEFUL PAGE
.useful-links {
  .component-header:after {
      content: '';
      background: #e7ad44;
      height: 2px;
      width: 40px;
      display: block;
      margin-top: 23px;
      margin-bottom: 20px;
    }
    .component-header {
      width: 100%;
      font-size: 36px;
      line-height: 120%;  
      color: #4a4a4a;
      font-weight: 700;
      font-style: normal;
      margin-bottom: 30px;
      text-transform: uppercase;
    }
  .useful {
    margin-bottom: 60px;
  }
  ul.post {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    li {
      min-height: 5rem;
    }
    li a {
      border: 1px solid #f5f5f5;
      text-align: center;
      padding: 25px;
      margin-right: 10px;
      display: flex;
    }
    li a:hover {
      background: #e7ad44;
      color: #fff;
    }
  }
}

// SUGGESTIONS PAGE
.suggestions {  
   .section-header {
    background: #eeece1;
    line-height: 4rem;
    font-size: 42px;
    padding: 10px;
    color: #c1935c;
    font-weight: bold;
   }
   .post {
      margin-bottom: 40px;
   }
   .post-image {
      margin-bottom: 15px;
   }
   .title {
      font-size: 34px;
      font-weight: bold;
      color: #2E4E75;
   }
}
.scrollspy-block {
   a {
		padding-left: 20px;
    color: #2e4e75;
	}
   a:hover {
      border-left: 1px solid #F44336;
		  padding-left: 19px;
      color: #768CA7;
   }
	a.active {
		border-left: 3px solid #F44336;
		padding-left: 18px;
    line-height: 2;
    font-weight: bold;
	}
}
