.gallery {
  .photo {
    flex-basis: 230px;
  }
  .room-title {
      width: span( 6 of 12 );
       @include susy-media(phone-only) { 
        width: span( 12 of 12 );
      }
       position: relative;
      z-index: 99;
      padding: 0 0 40px 0;
      margin: 10px 0;

          font-size: 36px;
    line-height: 120%;  
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
  }
  .room-title:after {
     @include susy-media(phone-only) { 
            top: 60%;
      }
      border-top: 2px solid #e7ad44;
      width: 60px;
      content: "";
      display: inline-block;
      vertical-align: middle;
      position: absolute;
      top: 50%;
      left: 0;
    }
    .material-placeholder {
      flex-basis: span(4 of 16);
      padding: gutters(16);
      @include susy-media(phone-only) { 
        flex-basis: span(16 of 16);
      }
  }
}
