.slideshow-section {

  // FRONTPAGE
  .frontpage-slideshow {
    margin-bottom: 80px;
  }

  .slideshow-image {
    // height: 500px;
  }

  .slideshow-title {
    box-shadow: 0 0px 100px 71px rgba(0, 0, 0, 0.25);
    background: transparent;
    background: rgba(0, 0, 0, 0.25);
    position: absolute;
    color: #fff;
    padding: 0 100px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;

    .title {
      position: relative;
      z-index: 99;
      flex-direction: column;
      font-weight: 700;
      line-height: 2;
      font-size: 64px;
      text-align: center;
    }

    @include susy-media(phone-only) {
      padding: 0 $section-spacing;

      .title {
        line-height: 1;
        font-size: 36px;
      }
    }

    .title:after {
      border-top: 2px solid #e7ad44;
      width: 25%;
      content: "";
      display: inline-block;
      vertical-align: middle;
      position: absolute;
      bottom: 10px;

      @include susy-media(phone-only) {
        bottom: -8px;
        ;
      }

      left: 38%;
    }

    .subtitle {
      font-size: 36px;
    }
  }

  .owl-theme .owl-nav {
    text-align: center;
    -webkit-tap-highlight-color: transparent;
    position: absolute;
    width: 100%;
    height: auto;
    top: 46%;
    display: flex;
    justify-content: space-between;
    padding: 0 25px;
    display: flex;

    @include susy-media(phone-only) {
      display: none;
    }
  }
}

.group-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.aboutus-section {
  .aboutus-frontpage {
    display: flex;
    flex-wrap: wrap;

    @include susy-media(phone-only) {
      flex-wrap: nowrap;
    }

    justify-content: space-between;

    h1 {
      font-weight: 700;
      color: #4a4a4a;
    }
  }

}

.awards-section {
  .component-header:after {
    content: '';
    background: #e7ad44;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  .component-header {
    width: 100%;
    font-size: 36px;
    line-height: 120%;
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .awards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .booking {
      @include susy-media(phone-only) {
        flex-basis: 100%;
        margin-bottom: gutters(4);
        padding: 20px;
      }

      display: flex;
      flex: 1;
      justify-content: center;
      flex-basis: span(4 of 12);
      max-width: 320px;
      flex-direction: column;
      border: 1px solid #e2e2e2;
      background: #024b79;
      font-weight: 700;
      font-size: 20px;
      font-size: 1.25rem;
      color: #fff;
      text-align: center;

      .field.score {
        font-size: 72px;
      }

      .message {
        font-size: 34px;
      }

      .image {
        img {
          width: 80%;
          margin: auto;
        }
      }
    }

    .sidebar-tripadvisor {
      flex: 1;
      max-width: 320px;
      flex-basis: span(4 of 12);
      border: 2px solid #589442;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;

      @include susy-media(phone-only) {
        flex-basis: 100%;
        margin-bottom: gutters(4);
        padding: 20px;
      }

      #CDSWIDPERPER {
        padding-top: 0px;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        color: #4b8439;
        display: flex;
        padding: gutters(4);
        margin-bottom: gutters(4);
        justify-content: center;

        #CDSWIDPERNUM {
          float: left;
          font: bold 28pt Arial, Verdana, "Bitstream Vera Sans", Helvetica, sans-serif;
          line-height: 1;
          letter-spacing: 0px;
        }

        #CDSWIDPERSYM {
          float: left;
          font: bold 16pt Arial, Verdana, "Bitstream Vera Sans", Helvetica, sans-serif;
          line-height: 1;
          letter-spacing: 0px;
          padding-top: 3px;
        }

        img {
          width: 30px;
        }
      }

      .hotel {
        flex: 1;
        flex-basis: span(4 of 12);
        max-width: 320px;
        padding: gutters(8);
        text-align: center;

        a {
          color: #323232;
          text-decoration: underline;
        }
      }

      .trip-logo img {
        width: 80%;
        margin: auto;
      }
    }

    .travel-myth {
      flex: 1;
      display: flex;
      flex-basis: span(4 of 12);
      max-width: 320px;

      @include susy-media(phone-only) {
        flex-basis: 100%;
        margin-bottom: gutters(4);
      }
    }
  }
}

// ROOMS
.rooms-section {
  ul.rooms-all {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    li {
      display: flex;
    }
  }

  @include susy-media(phone-only) {
    ul.rooms-all {
      flex-wrap: wrap;

      li {
        flex-basis: span(12 of 12);
        margin-bottom: 40px;

        .room-post {
          flex: 1;
        }
      }
    }
  }

  @include susy-media(tablet-portrait-up) {
    ul.rooms-all {
      flex-wrap: nowrap;
    }

    ul.rooms-all li {
      flex-basis: span(4 of 12 1.2);
    }
  }

  .component-header:after {
    content: '';
    background: #e7ad44;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  .component-header {
    width: 100%;
    font-size: 36px;
    line-height: 120%;
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .room-post {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .room-title {
    flex-grow: 1;
    padding: 34px 30px 5px;
  }

  .room-title a {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    margin: 0;
    margin-bottom: 7px;
    color: #4a4a4a;
  }

  .room-details {
    display: flex;
    padding: 0px 30px;

    .room-size {
      color: #8a8a8a;
      font-size: 15px;
    }

    .room-view {
      color: #8a8a8a;
      font-size: 15px;
    }

    .room-view:before {
      content: ' ';
      padding: 0 8px;
      text-align: center;
    }
  }

  .rates-from:before {
    content: '';
    background: #eee;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  .rates-from {
    color: #8a8a8a;
    font-size: 17px;
    font-weight: bold;
    line-height: 17px;
    padding: 0 30px 30px;
  }
}

// REVIEWS
.reviews-section {
  @include susy-media(phone-only) {
    background-image: url(/assets/images/reviews/600px/booking10.jpg);
  }

  @include susy-media(tablet-portrait-up) {
    background-image: url(/assets/images/reviews/900px/booking10.jpg);
  }

  @include susy-media(tablet-landscape-up) {
    background-image: url(/assets/images/reviews/1024px/booking10.jpg);
  }

  background-size: cover;
  padding: 50px;
  position: relative;
  margin-bottom: 80px;
}

.reviews {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;

  .component-header {
    width: span(6 of 12);

    @include susy-media(phone-only) {
      width: span(12 of 12);
    }

    color: #e7ad44;
    line-height: 1.6em;
    font-size: 18px;
    position: relative;
    z-index: 99;
    padding-left: 70px;
    margin: 30px 0;
  }

  .component-title {
    font-weight: bold;
    text-transform: uppercase;
  }

  .component-header:before {
    border-top: 2px solid #e7ad44;
    width: 60px;
    content: "";
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    top: 50%;
    left: 0;
  }

  .owl-carousel {
    width: span(6 of 12);

    @include susy-media(phone-only) {
      width: span(12 of 12);
    }

    margin-bottom: 30px;
    border: 1px solid #DDA32D;
  }

  .review {
    font-size: 18px;
    margin: 18px 0;
    padding: 30px;
    font-style: italic;
    line-height: 28px;
    color: #fff;
    font-weight: 300;
    z-index: 99;
  }

  .review-text {
    margin-bottom: 20px;
  }

  .name,
  .country {
    display: inline;
    font-weight: bold;
  }
}

.reviews-section:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #01244c;
  filter: alpha(opacity=51);
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=51)";
  -webkit-opacity: .51;
  -khtml-opacity: .51;
  -moz-opacity: .51;
  -ms-opacity: .51;
  -o-opacity: .51;
  opacity: .51;
}

// CONTACT FORM
.contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .component-header {
    width: 100%;
    font-size: 36px;
    line-height: 120%;
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .component-header:after {
    content: '';
    background: #e7ad44;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  form {
    flex-basis: span(8 of 12);

    @include susy-media(phone-only) {
      flex-basis: span(12 of 12);
      margin-bottom: $section-spacing;
    }
  }

  .booking-button-wrapper {
    background: #0d304e;
    color: #ffffff;
    font-size: 17px;
    font-size: 1.0625rem;
    line-height: 21px;
    line-height: 1.3125rem;
    font-weight: normal;
    border: 1px solid #ccc;
    padding: gutters(6);
    flex-basis: span(4 of 12 0.95);

    @include susy-media(phone-only) {
      flex-basis: span(12 of 12);
    }

    display: flex;
    flex-direction: column;

    img {
      margin-bottom: 10px;
      max-width: 250px;

    }

    .text {
      margin-bottom: 50px;
    }

    a {
      cursor: pointer;
      border-radius: 6px;
      padding: 5px 25px;
      line-height: 34px;
      font-size: 23px;
      border: 0;
      color: #fff;
      background-color: #0898ff;
      text-align: center;

    }

    a:hover {
      background-color: #003580;
    }
  }
}

.details-section {
  .component-header {
    width: 100%;
    font-size: 36px;
    line-height: 120%;
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .component-header:after {
    content: '';
    background: #e7ad44;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  .contact-details {
    flex-basis: span(4 of 12 0.95);

    @include susy-media(phone-only) {
      flex-basis: span(12 of 12);
    }

    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background: #fdfdfd;

    .field {
      margin: auto 0;
      display: flex;
      align-items: center;
      flex-basis: span(8 of 16);

      @include susy-media(phone-only) {
        flex-basis: span(12 of 12);
      }

      padding: $spacing-large 0;
      font-size: 18px;
    }

    i {
      padding: 0 10px;
      font-size: 42px;
      // color: #063c60;
      // color: #e7ad44;
      color: #0d304e;
    }
  }
}

// GALLERY
.photos-strip {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 80px;

  .component-header {
    width: 100%;
    font-size: 36px;
    line-height: 120%;
    color: #4a4a4a;
    font-weight: 700;
    font-style: normal;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .component-header:after {
    content: '';
    background: #e7ad44;
    height: 2px;
    width: 40px;
    display: block;
    margin-top: 23px;
    margin-bottom: 20px;
  }

  ul {
    display: flex;
    flex-wrap: nowrap;

    @include susy-media(phone-only) {
      flex-wrap: wrap;
      justify-content: center;

      li {
        flex-basis: span(8 of 16);
        flex-grow: 1;
      }

      li:last-child {
        flex-grow: 1;
      }
    }
  }
}

/*
 *  Default theme - Owl Carousel CSS File
 */

$color-base: #869791 !default;
$color-white: #FFF !default;
$color-gray: #D6D6D6 !default;

//nav

$nav-color: $color-white !default;
$nav-color-hover: $color-white !default;
$nav-font-size: 14px !default;
$nav-rounded: 3px !default;
$nav-margin: 5px !default;
$nav-padding: 4px 7px !default;
$nav-background: #024b79;
$nav-background-hover: $color-base !default;
$nav-disabled-opacity: 0.5 !default;

//dots

$dot-width: 10px !default;
$dot-height: 10px !default;
$dot-rounded: 30px !default;
$dot-margin: 5px 7px !default;
$dot-background: $color-gray !default;
$dot-background-active: $color-base !default;

.owl-theme {

  // Styling Next and Prev buttons
  .owl-nav {
    margin-top: 10px;
    text-align: center;
    -webkit-tap-highlight-color: transparent;

    [class*='owl-'] {
      color: $color-white;
      font-size: $nav-font-size;
      margin: $nav-margin;
      padding: $nav-padding;
      background: $nav-background;
      display: inline-block;
      cursor: pointer;
      border-radius: 3px;

      &:hover {
        background: $nav-background-hover;
        color: $nav-color-hover;
        text-decoration: none;
      }
    }

    .disabled {
      opacity: $nav-disabled-opacity;
      cursor: default;
    }
  }

  // Styling dots
  .owl-nav.disabled+.owl-dots {
    margin-top: 10px;
  }

  .owl-dots {
    text-align: center;
    -webkit-tap-highlight-color: transparent;

    .owl-dot {
      display: inline-block;
      zoom: 1;
      *display: inline;

      span {
        width: $dot-width;
        height: $dot-height;
        margin: $dot-margin;
        background: $dot-background;
        display: block;
        -webkit-backface-visibility: visible;
        transition: opacity 200ms ease;
        border-radius: 30px;
      }

      &.active,
      &:hover {
        span {
          background: $dot-background-active;
        }
      }
    }
  }
}

