.rates-section,
.reviews-rooms--section,
.photos-rooms--section,
.contact-rooms--section {
  max-width: 1152px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 6rem;
}
@media screen and (max-width: 599px) {
  .mobile-title-section,
  .description-section,
  .awards-section,
  .facilities-section,
  .rates-section,
  .reviews-rooms--section,
  .photos-rooms--section,
  .contact-rooms--section {
    padding: 0 2rem;
  }
}
@media screen and (min-width: 1800px) {
  .mobile-title-section,
  .description-section,
  .awards-section,
  .facilities-section,
  .rates-section,
  .reviews-rooms--section,
  .photos-rooms--section,
  .contact-rooms--section {
    padding: 0;
  }
}

@media screen and (max-width: 599px) {
  .facilities-section {
    margin: 0;
  }
}

.description-section {
  clear: both;
}

@media screen and (max-width: 599px) {
  .rooms > .footer-wrapper > .section-footer > .contact-footer {
    display: none;
  }
}

.rates-section {
  margin-top: 25px;
}

.gallery {
  margin-top: 25px;
}

/* MAP */
#map-canvas {
  width: 100%;
}

.map-section {
  clear: both;
  width: 98.75%;
  float: left;
  margin-left: 0.625%;
  margin-right: 0.625%;
  margin-bottom: 40px;
}

.contact-contact--section:after {
  content: " ";
  display: block;
  clear: both;
}
.contact-contact--section {
  max-width: 1152px;
  margin-left: auto;
  margin-right: auto;
  clear: both;
  margin-bottom: 10rem;
}

.useful-section {
  margin-top: 25px;
}
.useful-section:after {
  content: " ";
  display: block;
  clear: both;
}
.useful-section {
  max-width: 1152px;
  margin-left: auto;
  margin-right: auto;
  clear: both;
}

/* VILLAS GALLERY */
.gallery-slideshow {
  height: 100%;
}
.gallery-slideshow .post-image {
  align-items: flex-start;
}

.gallery-thumbnails {
  display: flex;
  flex-wrap: nowrap;
  position: absolute;
  bottom: 33px;
  z-index: 52;
  background: #fff;
}

.section-suggestions:after {
  content: " ";
  display: block;
  clear: both;
}
.section-suggestions {
  max-width: 1152px;
  margin-left: auto;
  margin-right: auto;
  clear: both;
}
.section-suggestions .main-suggestions {
  width: 67.5%;
  float: left;
  margin-left: 0.625%;
  margin-right: 0.625%;
}
@media screen and (max-width: 599px) {
  .section-suggestions .main-suggestions {
    width: 98.75%;
    float: left;
    margin-left: 0.625%;
    margin-right: 0.625%;
  }
  .section-suggestions .main-suggestions:after {
    content: " ";
    display: block;
    clear: both;
  }
  .section-suggestions .main-suggestions {
    max-width: 1152px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 6rem;
  }
}
.section-suggestions .sidebar-suggestions {
  width: 30%;
  float: right;
  margin-left: 0.625%;
  margin-right: 0.625%;
}
@media screen and (max-width: 599px) {
  .section-suggestions .sidebar-suggestions {
    display: none;
  }
}
.section-suggestions .scrollpsy-block {
  margin-top: 2em;
}

.footer:after {
  content: " ";
  display: block;
  clear: both;
}
.footer {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.recaptcha {
  width: 100%;
  float: left;
}

.hidden {
  display: none;
}