.success-message {
   display: none;
}

textarea {
    overflow-y: hidden;
    padding: 1.6rem 0;
    resize: none;
    min-height: 3rem;
}
/*  */
/* RESERVATION */
/*  */
.checkout,
.villa {
   @include span(6 of 12 last);
   margin-right: 0;
}
.checkin,
.persons {
   @include span(6 of 12);
   margin-left: 0;
}
.message,
.button-submit {
    @include span(100%);
}


.contact-details {
    // background: #0d304e ;
    // color: #8a8a8a;
    // color: #ffffff;
    font-size: 17px;
    line-height: 21px;
    font-weight: normal;
    border: 1px solid #e6e6e6;
    padding: gutters(8);
   .label {
       font-size: 17px;
       color: #323232;      
   }
   .phone,
   .email,
   .address,
   .mobile,
   .gps {
      font-size: 17px;
   }
}

.contact-contact--section {
  .contact-details {
    background: #0d304e;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    .field {
      align-items: center;
      display: flex;
    }
  }
  .material-icons {
    font-size: 36px;
    padding: 0 5px;
  }
}

/* OVERIDE PARSLEY */
.parsley-errors-list li {
    color: #f44336;
    opacity: 1;
    font-size: 0.8em;
}
/* OVERIDE FORM MATERIALIZE */
input[type="text"], input[type="password"], input[type="email"], input[type="url"], input[type="time"], input[type="date"], input[type="datetime-local"], input[type="tel"], input[type="number"], input[type="search"], textarea.materialize-textarea {
   margin-bottom: 5px;
}

.thankyou-section {
  .component-header {
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    padding: 10px;
  }
  .thankyou-text {
    background: #fdfdfd;
    border: 1px solid #e6e6e6;
    padding: 40px;
  }
  .field.thankyou {
    text-align: center;
    font-size: 24px;
  }
}