// HEADER
nav {
  background: #fff;
  .brand-logo {
    position: absolute;
    color: #024b79;
    font-weight: 700;
    display: inline-block;
    font-size: 1.4rem;
    padding: 0px;
    white-space: nowrap;

  }
  ul a {
    transition: background-color .3s;
    font-size: 1rem;
    color: #024b79;
    display: block;
    padding: 0 10px;
    cursor: pointer;
    font-weight: 700;
    text-transform: uppercase;
    i.right {
      float: right;
      margin-left: 0;
    }
  }
}
nav .button-collapse i {
        color: #024b79;
}
nav .button-collapse {
    margin: 0;
}
ul.side-nav {
    display: block;
  .brand-logo {
    position: relative;
    width: 100%;
    font-size: 16px;
    background: #1d5c84;
    text-align: center;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Merriweather','Cambria','Georgia','serif';
  }
    .collapsible{ 
    width: 100%;
    ul, li {
        width: 100%;
      }
    }
    .collapsible{ 
      width: 100%;
      ul {
          display: flex;
          flex-direction: column;
        }
      }

}
.dropdown-content li > a, .dropdown-content li > span {
    font-size: 16px;
    font-size: 1rem;
    color: #024b79;
    display: block;
    line-height: 22px;
    line-height: 1.375rem;
    padding: 14px 16px;
}
#sidenav-overlay{
  z-index: 996;
}
.side-nav a {
  font-size: 18px;
  line-height: 18px;
      display: flex;
    align-items: center;
}