.rates-section {
    .seasons {
        margin-bottom: 40px;
    }
    .header {
        display: flex;
        border: 1px solid #f5f5f5;
        background: #f5f5f5;
        .season {
            padding: 15px;       
            font-weight: bold;
        }
        .date {
            padding: 15px;
            font-size: 1.25rem;
            font-family: 'Merriweather';
            background: #464646;
            color: #fff;
            font-weight: bold;
        }
    }
    .content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .room {
        flex-basis: 0;
        flex: 1;
        border-left: 1px solid #f5f5f5;
        border-right: 1px solid #f5f5f5;
        border-bottom: 1px solid #ddd;
        .room-title a{
            padding: 5px 25px;
            font-size: 20px;
            border-bottom: 1px solid #ddd;
            color: #949494;
            display: flex;
        }
        .room-title  a:hover{
            color: #e7ad44;
            background: #464646;
        }
        .price {
            font-size: 30px;
            font-weight: bold;
            font-family: 'Merriweather','Cambria','Georgia','serif';
            color: #e7ad44;
            line-height: 2;
            background: #fff;
            padding: 5px 25px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
        }
        .price:hover {
            a.book {
                color: #323232;
                font-family: 'Roboto','Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
            }
        }
        a.book {
            font-size: 14px;
            font-weight: normal;
            font-family: 'Roboto','Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
            color: #eaeaea;
            display: flex;
            align-items: center;
        }
    }
    .room:first-child {
        margin-left: 0;
        border-right: none;
    }
    .room:last-child {
        margin-right: 0;
        border-left: none;
    }

}